import React, { useState,  useRef, useEffect } from 'react'
import socketIOClient from "socket.io-client";
const ENDPOINT = process.env.NODE_ENV === 'production' ? 'https://young-dawn-64939.herokuapp.com/' : "http://127.0.0.1:4001";


const View = ({currentUser}) => {

    const imgRef = useRef()
    const [sender, setSender] = useState(null)

    useEffect(() => {
        const socket = socketIOClient(ENDPOINT, {
            query: {
                chatID: currentUser
            }
        });
        socket.on('receive_message', (data) => {
            imgRef.current.src = data.videoStream
            setSender(data.senderChatID)
            //console.log(image);
        });
    }, [])

    return <div>
        <img ref={imgRef} width="150" height="120" alt="view" />
        {sender}
    </div>
}

export default View